import React, { useRef, useState } from "react";
import { Link } from "react-router-dom";
import Modal2 from "../../Components/UI/Modal2/Modal2";
import { useToastMessage } from "../../contexts/ToastMessageContext";
import { getBusinessInformation } from "../../api/Business";
import classes from "./Payment.module.css";

const Payment = (props) => {
  const { setErrorMessage } = useToastMessage();

  const [, setInfo] = useState(null);

  const getBusinessInformationRef = useRef(() => {});

  getBusinessInformationRef.current = () => {
    getBusinessInformation(props.match.params.businessId)
      .then((data) => {
        if (!data.error) setInfo(data.data);
        else {
          setErrorMessage(data.error);
          return props.history.goBack();
        }
      })
      .catch((err) => setErrorMessage(err));
  };
  return (
    <section className={classes.Payment}>
      <Modal2 title="Payments" />

      <main className={classes.Payments_Main}>
        <ul>
          {/* <li>
            <Link to={`/user/payments/renewals`}>Payment Accounts</Link>
          </li> */}
          <li>
            <Link
              to={`/user/payments/history/${props.match.params.businessId}`}
            >
              Payment History
            </Link>
          </li>
          <li>
            <Link
              to={`/user/payments/renewals/${props.match.params.businessId}`}
            >
              Renewals
            </Link>
          </li>
        </ul>
      </main>
    </section>
  );
};

export default Payment;
